.caption {
  text-transform: uppercase;
  font-weight: @medium;
  &--heavy {
    font-weight: @heavy;
  }
  &--large {
    font-size: 2rem;
  }
}

.title {
  font-weight: @heavy;
  font-size: 5rem;
  line-height: 5.4rem;
  &--large {
    font-size: 6rem;
    line-height: 6.5rem;
  }
  &--small {
    font-size: 4rem;
    line-height: 4.8rem;
  }
}

.subtitle {
  font-weight: @heavy;
  font-size: 2.4rem;
  // line-height: 3.3rem;
}

.desc {
  font-size: 1.9rem;
  &--large {
    font-size: 2.1rem;
  }
}

@media (max-width: @tablet-width) {
  .caption {
    font-size: 1.2rem;
    &--large {
      // font-size: 2rem;
    }
  }

  .title {
    font-size: 2.7rem;
    line-height: initial;
    &--large {
      font-size: 3.2rem;
      line-height: 3.1rem;
    }
    &--small {
      // font-size: 4rem;
      // line-height: 4.8rem;
    }
  }

  .subtitle {
    // font-size: 1.6rem;
    font-size: 2rem;
  }

  .desc {
    font-size: 1.6rem;
    &--large {
      // font-size: 2.1rem;
    }
  }
}
